import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import Images from './images';
import '../styles/ultraBoost.scss';

const UltraBoost = () =>{
    const urls = Images.Shoes;
    const { ultraboost_back, ultraboost_back_potrait } = urls;
    const [imgSrc, changeImgSrc] = useState(ultraboost_back);
    var images = Object.values(urls);
    images = images.slice(0, 7);
    const [names] = useState([
        'Ultraboost 20 Running Shoes - AW20',
        'Ultraboost PB Running Shoes - SS20',
        'Ultraboost 21 Limited Edition Running Shoes - SS21',
        'Ultraboost 20 Women\'s Running Shoes - SS21',
        'Ultraboost 21 Women\'s Running Shoes - SS21',
        'Ultraboost 21 Women\'s Running Shoes - SS21',
        'Ultraboost 21 Primeblue Running Shoes - SS21'
    ]);
    const resizeFunc = () =>{
        if(window.innerWidth <= 650) changeImgSrc(ultraboost_back_potrait);
        else changeImgSrc(ultraboost_back);
    }   
    useEffect(() =>{
        resizeFunc();
        window.addEventListener('resize',resizeFunc);
    }, [])
    return(
        <div className="ultraBoost scrollEffect">
            <div className="heading">
                <p>UltraBoost</p>
                <div className="lineColored">
                    <div className="one"></div>
                    <div className="two"></div>
                    <div className="three"></div>
                    <div className="four"></div>
                </div>
            </div>
            <Link to='/adidas/ultraBoost'>
                <img src={imgSrc} alt="ULTRABOOST BACK" className="ultraCover" />
            </Link>
            <p className="reason">Running Shoes</p>
            <p className="reasonBelow">
                A new day. A new run. Make it your best. These high-performance shoes feature a foot-hugging knit upper. Stitched-in reinforcement is precisely placed to give you support in the places you need it most.
            </p>
            <p className="two">Models</p>
            <div className="myContainer scrollEffect">
                {
                    images.map((model, index) =>
                        <div className="shoe" key={model}>
                            <div className="img">
                                <img
                                    src={model}
                                    alt="NIKE"
                                />
                            </div>
                            <div className="info">
                                <p className="name">{names[index].slice(0, 45)}</p>
                                <div className="line"></div>
                                <div>
                                    <p className="price">
                                    <strong>120$</strong>
                                    </p>
                                    <p className="extraDetail">Sustainable Material</p>
                                </div>
                            </div>
                        </div>
                    )
                }
            </div>
        </div>
    )
}

export default UltraBoost;