import React, { useLayoutEffect, useState } from 'react';
import { Helmet } from 'react-helmet';
import Carousel from 'react-bootstrap/Carousel';
import Image from 'react-bootstrap/Image';
import Shoes from './images/Shoes';
import Icons from './images/Icons';
import Misc from './images/Misc';
import TrendingNow from './TrendingNow';
import UltraBoost from './UltraBoost';
import Jordan from './Jordan';
import Categories from './Categories';
import '../styles/home.scss';


const Home = () =>{
    const [sideMenuLeft, changeMenuLeft] = useState("-300px");
    const [infoIndex, changeInfoIndex] = useState(0);
    const onslide = () =>{
        if(infoIndex === 2) changeInfoIndex(0);
        else changeInfoIndex(infoIndex + 1);
    }
    const hamburger = () =>{
        var overlay = document.querySelector("div.overlay");
        changeMenuLeft(sideMenuLeft ? 0 : "-300px");
        if(sideMenuLeft === 0){
            overlay.style.display = 'none';
        } else {
            overlay.style.display = 'block';
        }
    }
    const close = () =>{
        var overlay = document.querySelector("div.overlay");
        overlay.style.display = 'none';
        changeMenuLeft("-300px");
    }
    useLayoutEffect(() =>{
        const sizeChange = () =>{
            if(window.innerWidth >= 768){
                close();
            }
        }
        window.addEventListener('resize',sizeChange);
        sizeChange();
        return () => window.removeEventListener('resize', sizeChange);
    }, [])
    return(
        <div className="Home" id="home">
            <Helmet>
                <title>Home</title>
            </Helmet>
            <div className="myContainer fadeEffect">
                <div className="sideMenu" style={{"left":sideMenuLeft}}>
                    <div className="close" onClick={() => close()}>
                        <img src={Icons.back} alt="CLOSE" />
                    </div>
                    <div className="user">
                        <Image src={Misc.itachi_dp} roundedCircle alt="USER" />
                        <p className="name"><strong>Abdus Samad</strong></p>
                        <p className="location">San Francisco, CA</p>
                    </div>
                    <div className="sideMenuLinks">
                        <a href="#Home">Home</a>
                        <a href="#Collection">Collection</a>
                        <a href="#Shoes">Shoes</a>
                        <a href="#Running Shoes">Running Shoes</a>
                        <a href="#Login">Login</a>
                    </div>
                </div>
                <nav>
                    <a href="#home" className="brand">Kutsu</a>
                    <div className="links">
                        <a href="#Home">Home</a>
                        <a href="#Collection">Collection</a>
                        <a href="#Shoes">Shoes</a>
                        <a href="#Running Shoes">Running Shoes</a>
                        <a href="#Login">Login</a>
                    </div>
                    <div className="hamburger" onClick={() => hamburger()}>
                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 30 30" width="30" height="30" focusable="false"><title>Menu</title><path stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeMiterlimit="10" d="M4 7h22M4 15h22M4 23h22"></path></svg>
                    </div>
                </nav>
                <div className="jumbshoes">
                    <div className="info">
                        <Carousel controls={false} fade activeIndex={infoIndex}>
                            <Carousel.Item>
                                <p className="type"><strong>New Basketball Shoes</strong></p>
                                <p className="name"><strong>Zion 1</strong></p>
                                <p className="description">With a down-to-earth persona and abilities that are out of this world, Zion is unlike anybody else. On court, the gentle spirit who's all about family transforms into an unmatched force of unstoppable athleticism and speed.</p>
                            </Carousel.Item>
                            <Carousel.Item>
                                <p className="type">Air Jordan</p>
                                <p className="name">XII Retro Black</p>
                                <p className="description">Michael Jordan’s creative dexterity and stealth put his opponents on edge and threw them off balance, earning him the infamous nickname “Black Cat”. Throughout the 1997–98 season Jordan consistently outmaneuvered his rivals with a remarkable deftness all his own.</p>
                            </Carousel.Item>
                            <Carousel.Item>
                                <p className="type">New Balance Kids</p>
                                <p className="name">Roav Tee</p>
                                <p className="description">Built for little runners, our Fresh Foam Roav kids' running shoes feature a Fresh Foam midsole for enhanced cushioning where they need it most, offering added comfort while they sprint and play.</p>
                            </Carousel.Item>
                        </Carousel>
                    </div>
                    <div className="shoe">
                        <Carousel controls={false} onSlide={() => onslide()}>
                            <Carousel.Item>
                                <img src={Shoes.zion2} alt="ZION2"/>
                            </Carousel.Item>
                            <Carousel.Item>
                                <img src={Shoes.air_jordan_xii_rb} alt="AirJordan3" />
                            </Carousel.Item>
                            <Carousel.Item>
                                <img src={Shoes.roav_tee} alt="Roav Tee" />
                            </Carousel.Item>
                        </Carousel>                        
                    </div>
                </div>
            </div>
            <a className="scrollToTop" href="#home">
                <img src={Icons.back} alt="close" />
            </a>
            <TrendingNow />
            <UltraBoost />
            <Jordan />
            <Categories />
        </div>
    )
}

export default Home;