import React, { useEffect } from 'react';
import { Switch, Route } from 'react-router-dom';
// import Footer from './Components/Footer';
import Home from './Components/Home';
import Icons from './Components/images/Icons';
import './styles/global.scss';

const App = () => {
    // var lastScrollTop = 0;
    // var bodyTranslate = 0;
    const visible = function(elem) {
        var diff = 350;
        var top = getElemDistance(elem);
        if(window.innerWidth <= 800){
            diff = 450;
        }
        return (top <= window.scrollY + (diff));
    };
    const getElemDistance = ( elem ) => {
        var location = 0;
        if (elem.offsetParent) {
            do {
                location += elem.offsetTop;
                elem = elem.offsetParent;
            } while (elem);
        }
        return location >= 0 ? location : 0;
    };
    const scrollingEffect = () =>{
        // var st = window.scrollY;
        // if(st !== 0){
        //     if (st > lastScrollTop){
        //         document.body.style.transform = `translateY(${bodyTranslate - 100}px)`;
        //         bodyTranslate -= 100;
        //     } else {
        //         document.body.style.transform = `translateY(${bodyTranslate + 100}px)`;
        //         bodyTranslate += 100;
        //     }
        // }
        // lastScrollTop = st;
        var scrollToTop = document.querySelector('a.scrollToTop');
        if(window.scrollY <= 40) scrollToTop.style.opacity = 0;
        else scrollToTop.style.opacity = 0.75;
        var scrollEffect = document.querySelectorAll('.scrollEffect');
        for(var x = 0;x < scrollEffect.length;x++){
            if(visible(scrollEffect[x])){
                if(!scrollEffect[x].classList.contains('alreadyVisible')){
                    scrollEffect[x].classList.add('come-in','alreadyVisible');
                }
            }
        }
        var fadeEffect = document.querySelectorAll('.fadeEffect');
        for(x = 0;x < fadeEffect.length;x++){
            if(visible(fadeEffect[x])){
                if(!fadeEffect[x].classList.contains('alreadyVisible')){
                    fadeEffect[x].classList.add('fade-in','alreadyVisible');
                }
            }
        }
    }
    useEffect(() =>{
        scrollingEffect();
        window.addEventListener('scroll',scrollingEffect);
    }, []);
    return(
        <div className="App">
            <Switch>
                <Route path="/" exact>
                    <Home />
                </Route>
            </Switch>
            {/* <Footer /> */}
        </div>
    )
}

export default App;