import React from 'react';
import { Link } from 'react-router-dom';
import './Button.scss';

const Button = (props) =>{
    return(
            <Link
                to={props.location}
                className="Button"
                style={{
                    "color":props.color,
                    "borderColor":props.borderColor
                }}    
            >{props.text}</Link>
    )
}

export default Button;