import Carousel from 'react-bootstrap/Carousel';
import React from 'react';
import Button from './reusables/Button';
import Shoes from './images/Shoes';
import '../styles/TrendingNow.scss';

const TrendingNow = () =>{
    return(
        <div className="TrendingNow">
            <p className="header">Trending Now</p>
            <div className="line"></div>
            <div className="nikeShoe scrollEffect">
                <div className="logo">
                    <svg className="pre-logo-svg" height="120px" width="120px" fill="#111" viewBox="0 0 69 32"><path d="M68.56 4L18.4 25.36Q12.16 28 7.92 28q-4.8 0-6.96-3.36-1.36-2.16-.8-5.48t2.96-7.08q2-3.04 6.56-8-1.6 2.56-2.24 5.28-1.2 5.12 2.16 7.52Q11.2 18 14 18q2.24 0 5.04-.72z"></path></svg>
                </div>
                <p className="head">Created By You</p>
                <h2>Nike Air Force 1</h2>
                <div className="flex">
                    <div className="imgs">
                        <Carousel controls={false}>
                            <Carousel.Item>
                                <img src={Shoes.naf_black} alt="NAF BLACK" />
                            </Carousel.Item>
                            <Carousel.Item>
                                <img src={Shoes.naf_blue} alt="NAF BLUE" />
                            </Carousel.Item>
                            <Carousel.Item>
                                <img src={Shoes.naf_white} alt="NAF WHITE" />
                            </Carousel.Item>
                        </Carousel>
                    </div>
                    <div className="info">
                        <p>The Nike Air Force 1 High By You became an instant icon after its debut in 1982. Now you can make this classic your own with a color palette inspired by the ‘80s and ‘90s and premium materials including smooth and rippled leather and a new, matching sidewall selection. It’s time to create a look that speaks to you.</p>
                        <br />
                        <Button text="Read More" link location='/read' color="black" borderColor="black" />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default TrendingNow;